body {
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 34px;
  color: #757575;
}

@media (min-width: 1200px) {
  .container {
    max-width: 960px;
  }
}

h1 {
  font-size: 60px;
  font-weight: 700;
}

h3 {
  font-size: 30px;
  font-weight: 900;
  color: #212121;
}

#hero {
  text-align: center;
  color: #1a1a1a;
}

.hero {
  min-height: 100vh;
}

section.content {
  padding: 50px 0 70px 0;
}
.prompt-text {
  font-size: 20px;
  line-height: 20px;
  font-weight: 400;
  color: #353535;
}

.scroll-icon {
  color: #474747;
}

#what ul {
  padding-left: 0;
  list-style: none;
}

.list-item {
  font-size: 14px;
  font-weight: 400;
  color: #a3a3a3;
}

.list-item-symbol {
  color: #e5e5e5;
  padding: 0 10px 0 0;
}

.card-title {
  display: flex;
  justify-content: space-between;
  line-height: 48px;
  font-weight: 500;
}

.job-description {
  color: #a3a3a3;
}

#where .list-group-item {
  color: #a3a3a3;
}

#where .list-group-item-action:focus, .list-group-item-action:hover {
  color: #757575 !important;
  background-color: unset;
}

#where .list-group-item.active {
  background-color: unset;
}

.top-effects {
  z-index: 1;
  position: absolute;
  width: 100%;
  left: 0;
  height: 10px;
  opacity: .6;
  background: radial-gradient(ellipse at 50% 0,rgba(0,0,0,.3) 0,transparent 70%);
}

.footer-text {
  padding: 5px 0;
}
#footer {
  background-color: #f9f9f9;
  text-align: center;
  font-size: 10px;
  font-weight: 400;
  letter-spacing: 0.5px;
}
